/* grid for news */
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 10px;
  grid-gap: 20pt;
  gap: 20pt;
}
.grid-item {
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid #b7b7b7;
  border-radius: 10pt;
  text-align: center;
}
.grid-item .description .detail p {
  color: #b7b7b7;
  font-size: 10pt;
}
.grid-item .description {
  padding: 15pt;
  grid-gap: 5pt;
  gap: 5pt;
  display: grid;
}
.grid-item p {
  color: #006d7c;
  text-align: left;
  font-size: 12pt;
}
.grid-item h1 {
  color: #006d7c;
  font-size: 16pt;
  font-weight: 600;
  text-align: left;
}
.grid-item img {
  border-radius: 10pt;
  width: 300pt;
  height: 200pt;
  object-fit: cover;
}
/* split content */
