.loginbox {
  min-height: 400px;
  width: 420px;
  background-color: white;
  padding: 20pt;
  padding-bottom: 0pt;
  border-radius: 20px;
  margin: 40pt;
}
.login-header {
  grid-area: header;
  justify-content: left;
  align-items: center;
  display: flex;
}
.login-logo {
  justify-content: right;
  display: flex;
  align-items: center;
  grid-area: logo;
}
.login-logo img {
  width: 110pt;
}
.login-form {
  grid-area: form;
}
.login-form .inputbox {
  position: relative;
}
.login-form .inputbox input {
  border: none;
  padding: 5px 0;
  outline: none;
  width: 100%;
  margin-bottom: 30pt;
  background: transparent;
  border-bottom: 1px solid #b7b7b7;
  font-size: 14pt;
  text-decoration: none;
}
.login-form .inputbox label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 0;
  font-size: 14pt;
  color: #b7b7b7;
  pointer-events: none;
  transition: 0.5s;
}
.login-form .inputbox input[type="password"] {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.login-form .inputbox input:focus ~ label,
.login-form .inputbox input:hover ~ label,
.login-form .inputbox input:valid ~ label {
  top: -15pt;
  left: 0;
  color: #006d7c;
  font-size: 11pt;
}
.login-form .inputbox input:focus,
.login-form .inputbox input:valid {
  border-bottom: 1px solid #079cb0;
}
.loginbox .grid-container-admin {
  display: grid;
  grid-template-areas:
    "header logo"
    "form form";
  grid-gap: 10pt;
}
.loginbox h1 {
  font-size: 28pt;
  color: #006d7c;
  font-weight: 600;
}
.loginbox button {
  width: 100%;
  height: 35pt;
  outline: none;
  border: none;
  background-color: #006d7c;
  color: white;
  font-weight: 500;
  font-size: 14pt;
  border-radius: 10pt;
  cursor: pointer;
  transition: 0.5s;
}
.loginbox button:hover {
  background-color: #079cb0;
}

.Header {
  position: fixed;
  top: 0; /* Stick it to the top */
  height: 70px;
  width: 100vw;
  font-size: 14pt;
  display: grid;
  grid-template-areas: "logo nav";
  align-items: center;
  /* Cosmetics */
  background-color: #ffffff;
  border-bottom: 1pt solid #079cb0;
}

.Logo {
  grid-area: logo;
  padding-left: 10pt;
}

.Nav {
  display: grid;
  grid-area: nav;
  grid-template-columns: repeat(5, auto);
  align-items: center;
  justify-items: center;
}
.Nav .item {
  color: #079cb0;
  font-weight: 500;
  transition: 0.2s;
  text-decoration: none;
}
.Nav .item .ho:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  color: #079cb0;
  transition: 0.1s;
}
.Nav button {
  padding: 10px;
  outline: none;
  border: none;
  color: #fff;
  font-weight: 600;
  background-color: rgb(255, 0, 0);
  box-shadow: 0px 5px 0px 0px rgba(255, 0, 0, 0.25);
  border-radius: 10px;
  cursor: pointer;
  transition: 70ms;
}

.Nav button:active {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  box-shadow: 0px 2px 0px 0px rgba(255, 0, 0, 0.25);
}

.Burger {
  display: none;
  grid-area: burger;
  margin: 10pt 20pt 0 0;
  padding: 0;
  justify-self: end;
  border: none;
  background: none;
  font-size: 14pt;
  font-weight: 500;
  color: #079cb0;
  outline: none;
  transition: 0.1s;
}
.Burger:active {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media (max-width: 700px) {
  .Header {
    align-items: center;
    height: 70px;
    grid-template-areas: "logo burger" "nav nav";
  }
  .Nav {
    grid-template-rows: repeat(4, auto);
    grid-template-columns: none;
    grid-row-gap: 20px;

    padding: 30pt 0 20pt 0;
    background: rgb(255, 255, 255);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.082);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .Burger {
    display: inline;
    cursor: pointer;
  }

  .Logo {
    height: inherit;
    padding-top: 7pt;
  }
}

.NavAnimation-enter {
  opacity: 0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.NavAnimation-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: opacity 350ms, -webkit-transform 350ms;
  transition: opacity 350ms, transform 350ms;
  transition: opacity 350ms, transform 350ms, -webkit-transform 350ms;
}
.NavAnimation-exit {
  opacity: 1;
}
.NavAnimation-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  transition: opacity 350ms, -webkit-transform 350ms;
  transition: opacity 350ms, transform 350ms;
  transition: opacity 350ms, transform 350ms, -webkit-transform 350ms;
}

